@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&family=Black+Ops+One&family=Changa:wght@200&family=Luckiest+Guy&family=Orelega+One&family=PT+Sans&family=Paytone+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100&display=swap);

:root {
  --box-shadow-blue: 0px 0px 5px #0077ff99, 0 0px 2px rgba(0, 31, 204, 0.8);
  --box-shadow-blue-selected: 
      1px 1px 5px #dbecff,
      -1px -1px 5px #91c4ff,
      0px 0px 5px #0077ff99,
      0px 0px 10px #0077ff99,
      0px 0px 15px #0077ff99,
      0px 0px 20px #0077ff99;

  --neon-border-color: #0077ff99;

  --box-shadow-white: 1px 1px 5px rgba(255, 255, 255, 0.4), 0 1px 2px rgba(255, 255, 255, 0.5);

  --background-dark: #000307;
  --forebround-blue: #74b5ff;
  --forebround-light-blue: #b7d9ff;
  --forebround-blue-faded: #006deb88;

  }

body{
  /* background-color: #242b36; */
  background-color: #141a24;
  font-family: 'Kanit', sans-serif;
  color: #bdc2d4;
  margin: 0;
  background-image: url("https://www.transparenttextures.com/patterns/3px-tile.png");
}

.container {
    margin: auto;
    width: 100%;
}

.prison-door {

  margin: 1em 0.5em 0em 0.5em;
  border-radius: 20px 20px 0px 0px;
      background-color: #000307;
      background-color: var(--background-dark);
    display: -webkit-flex;
    display: flex;
    /* justify-content: space-between; */
    height: 270px;
    width: 13em;

    /* Major performance reduction -->  */
    /* box-shadow: var(--box-shadow-blue); */

    --text-shadow-prison-number: none;
    --prison-number-color: var(--forebround-blue-faded);
    --prison-number-color-hover: var(--forebround-blue);
  }

  .prison-door.is-selected  {
    --text-shadow-prison-number: 
      0px 0px 5px #006debff,
      0px 0px 10px #006debff,
      0px 0px 15px #006debff,
      0px 0px 25px #006debff,
      0px 0px 35px #006debff,
      0px 0px 50px #006debff,
      0px 0px 70px #006debff;
    --prison-number-color: rgb(218, 235, 255);
    --prison-number-color-hover: rgb(218, 235, 255);;

    box-shadow: 1px 1px 5px #dbecff,
      -1px -1px 5px #91c4ff,
      0px 0px 5px #0077ff99,
      0px 0px 10px #0077ff99,
      0px 0px 15px #0077ff99,
      0px 0px 20px #0077ff99;;

    box-shadow: var(--box-shadow-blue-selected);
  }

  .glow{
    text-shadow: var(--text-shadow-prison-number);
    color: var(--prison-number-color);
  }

  .glow:hover{
    color: var(--prison-number-color-hover);
    cursor: pointer;
  }

  .prison-door > div {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
  }

  .prison-door-inner-border{
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px #0077ff99, 0 0px 2px rgba(0, 31, 204, 0.8);
    box-shadow: var(--box-shadow-blue);
    width: 100%;
  }
  
  .prison-grate{
    margin: 20px 10% 0 10%;
    height: 80px;
    border-radius: 10px;
    background-color: #343434;
  }

  .prison-grate > div{
    box-sizing: border-box;
    background-color: #aaaacc;
    height: 100%;
    width: 7%;
    margin: 0 10px;
    display: inline-block;
    border-right: 2px solid white;
    border-left: 2px solid gray;
  }

  .prisoner-number{
    font-family: 'Changa', sans-serif;
    font-size: 30px;
    margin: 10px 30px;
  }

  .engraved {
    background-color: black;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
    text-shadow: rgba(200,200,200,0.9) -1px 1px 1px;
 }
 
 .details-container{
   margin: auto;
  border-radius: 20px 20px 0px 0px;
  background-color: #000307;
  background-color: var(--background-dark);
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-between; */
  width: 50%;
  color: #74b5ff;
  color: var(--forebround-blue);

 }

 .left-container{
  -webkit-flex: 0 1 50%;
          flex: 0 1 50%;
  min-height: 100%;
  max-width: 50%;
 }

 .right-container{
  -webkit-flex: 0 1 50%;
          flex: 0 1 50%;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  max-width: 50%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  box-sizing: border-box;
 }

 .picture-background{
    background: #006deb88;
    background: var(--forebround-blue-faded);
    padding: 0.5em;
 }

 .flex{
   display: -webkit-flex;
   display: flex;
 }

 img{
  object-fit: contain;
  width: 50%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
 }

 hr{
  /* --box-shadow-blue-selected: 
  1px 1px 5px #dbecff,
  -1px -1px 5px #91c4ff,
  0px 0px 5px #0077ff99,
  0px 0px 10px #0077ff99,
  0px 0px 15px #0077ff99,
  0px 0px 20px #0077ff99; */
  box-shadow: 1px 1px 5px #dbecff,
      -1px -1px 5px #91c4ff,
      0px 0px 5px #0077ff99,
      0px 0px 10px #0077ff99,
      0px 0px 15px #0077ff99,
      0px 0px 20px #0077ff99;
  box-shadow: var(--box-shadow-blue-selected);
  /* border: 1px solid red; */
  border-color: #b7d9ff;
  border-color: var(--forebround-light-blue);
 }



 



